import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core'
import { AssetModelxService, AssetLifeLimitService, AssetConditionMonitoringService, AssetPartIdService, VendorVendorService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Modelx, Vendor, Life_limit, Condition_monitoring, Part_id } from 'src/app/sdk/asset/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    constructor(
        private assetModelxService: AssetModelxService,
        private assetLifeLimitService: AssetLifeLimitService,
        private assetConditionMonitoringService: AssetConditionMonitoringService,
        private assetPartIdService: AssetPartIdService,
        private vendorVendorService: VendorVendorService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedroute: ActivatedRoute,
    ) { }

    data: Modelx

    id_model: string = ''

    listVendor: Vendor[] = []
    listLifeLimit: Life_limit[] = []
    listCondition: Condition_monitoring[] = []
    listType: Part_id[] = []

    ngOnInit() {
        this.id_model = this.activatedroute.snapshot.paramMap.get('id_model')
        this.getPreeData()
        this.form()
        this.getData()
    }

    getPreeData() {
        this.assetPartIdService.getAssetPartIds().subscribe(data => {
            this.listType = data.data
        })

        this.assetConditionMonitoringService.getAssetConditionMonitorings().subscribe(data => {
            this.listCondition = data.data
        })

        this.assetLifeLimitService.getAssetLifeLimits().subscribe(data => {
            this.listLifeLimit = data.data
        })

        this.vendorVendorService.getVendorVendors().subscribe(data => {
            this.listVendor = data.data
        })
    }

    getData() {
        this.spinner.show()
        this.assetModelxService.getAssetModelxesId(this.id_model).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }

    dataForm: FormGroupTypeSafe<Modelx>
    form() {
        this.dataForm = this.fb.group<Modelx>({
            id_manufacture: new FormControl('', [Validators.required]),
            id_condition_monitoring: new FormControl('', [Validators.required]),
            id_life_limit: new FormControl('', [Validators.required]),
            id_part_id: new FormControl('', [Validators.required]),
            part_number: new FormControl(''),
            keyword: new FormControl(''),
            life_limit_var: new FormControl(''),
            life_limit_val: new FormControl(''),
            min_qty: new FormControl(''),
            unit: new FormControl(''),
            id_user: new FormControl(''),
            id_model: new FormControl(''),
            is_single: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            foto: new FormControl(''),
        })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.assetModelxService.putAssetModelxesId({ id: this.id_model, body: this.data }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/asset/master/model/list'])
    }
}
